
import React, { useState, useEffect } from 'react';

const ProfileStatusWhithHooks = (props) => {
  const [editMode, setEditMode] = useState(false);
  const [status, setStatus] = useState(props.status);

  const handleActivateEditMode = () => {
    setEditMode(true);
  };

  const handleDeactivateEditMode = () => {
    setEditMode(false);
    props.updateStatus(status);
  };

  const handleStatusChange = (e) => {
    setStatus(e.target.value);
  };

  useEffect(() => {
setStatus(props.status)
  }, [props.status]);

  return (
    <>
    {!editMode ? (
      <span onClick={handleActivateEditMode}>
        {props.status || "Status: К сожалению, статуса нет..."}
      </span>
    ) : (
      <input
        onChange={handleStatusChange}
        value={status}
        autoFocus={true}
        onBlur={handleDeactivateEditMode}
      ></input>
    )}
  </>
  )
}

export default ProfileStatusWhithHooks;

